body {
  letter-spacing: .03px;
  background-color: var(--bgc-third);
  font-family: Roboto, sans-serif;
  font-size: 14px;
  line-height: 1.17;
}

@media screen and (max-width: 1139px) {
  body.modal-open {
    overflow: hidden;
  }
}

html, body {
  height: 100%;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.container {
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 480px) {
  .container {
    width: 440px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 700px;
    padding-left: 34px;
    padding-right: 34px;
  }
}

@media screen and (min-width: 1140px) {
  .container {
    width: 1110px;
    padding-left: 15px;
    padding-right: 15px;
  }
}

.section {
  padding-top: 30px;
  padding-bottom: 30px;
}

.footer-fixed-wrapper {
  height: calc(100% + 1px);
  flex-direction: column;
  padding-top: 60px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .footer-fixed-wrapper {
    padding-top: 80px;
  }
}

.main-content {
  flex-grow: 1;
}

.logo {
  max-width: 200px;
  letter-spacing: .05em;
  align-items: center;
  padding-top: 7px;
  padding-bottom: 7px;
  font-family: Roboto Slab, sans-serif;
  font-size: 20px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .logo {
    padding-top: 15px;
    padding-bottom: 15px;
  }
}

.logo__img--animate {
  animation: logo 1000ms var(--animation-timing-function);
}

@keyframes logo {
  0% {
    transform: scale(1.2);
  }

  100% {
    transform: scale(1);
  }
}

.logo__text {
  text-align: center;
  color: var(--text-logo);
}

.lang-menu {
  display: flex;
}

@media screen and (min-width: 768px) and (max-width: 1139px) {
  .lang-menu {
    justify-content: center;
  }
}

@media screen and (max-width: 1139px) {
  .lang-menu {
    margin-top: 20px;
  }
}

.lang-menu__item {
  display: flex;
}

.lang-menu__item:not(:last-child) {
  margin-right: 5px;
}

.lang-menu__item:not(:last-child):after {
  content: "";
  width: 1px;
  height: 50%;
  background-color: var(--text-main);
  margin-left: 5px;
  display: block;
  transform: translateY(50%);
}

.lang-menu__btn {
  text-transform: uppercase;
  color: var(--text-main);
  transition: background-color var(--animation-duration) var(--animation-timing-function), color var(--animation-duration) var(--animation-timing-function);
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid rgba(0, 0, 0, 0);
  border-radius: 5px 10px;
  padding: 10px;
  font-size: 20px;
}

.lang-menu__btn:hover {
  color: var(--bgc-main);
  background-color: var(--accent-color);
}

@media screen and (min-width: 1140px) {
  .lang-menu__btn {
    border-radius: 10px 5px;
  }
}

.socials {
  justify-content: center;
  align-items: center;
  padding-top: 10px;
  padding-bottom: 10px;
  display: flex;
}

.socials__item {
  width: 44px;
  height: 44px;
}

.socials__item:not(:first-child) {
  margin-left: 20px;
}

.socials__link {
  width: 100%;
  height: 100%;
  color: var(--accent-color);
  background-color: var(--main-blue);
  transition: color var(--animation-duration) var(--animation-timing-function), background-color var(--animation-duration) var(--animation-timing-function);
  border: none;
  border-radius: 20px 10px;
  justify-content: center;
  align-items: center;
  display: flex;
}

.socials__link:hover, .socials__link:focus {
  color: var(--main-black);
  background-color: var(--accent-color);
}

.socials__icon {
  fill: currentColor;
}

.top-btn {
  z-index: 4;
  color: #000;
  background-color: var(--accent-color-transparent);
  transition: background-color var(--animation-duration) var(--animation-timing-function), color var(--animation-duration) var(--animation-timing-function);
  border-radius: 100px 0 0;
  outline: none;
  flex-direction: column;
  align-items: center;
  padding: 5px 5px 5px 10px;
  font-size: 14px;
  display: none;
  position: fixed;
  bottom: 0;
  right: 0;
}

@media screen and (min-width: 768px) {
  .top-btn {
    padding: 12px 12px 12px 25px;
  }
}

.top-btn:hover {
  color: var(--accent-color);
  background-color: var(--main-blue);
}

.top-btn__icon {
  width: 30px;
  fill: currentColor;
}

.btn {
  letter-spacing: 2px;
  color: var(--form-text-color-main);
  background-color: var(--form-accent-main);
  box-shadow: 3px 5px 0 var(--form-sumbit-shadow);
  transition: background-color var(--animation-duration) var(--animation-timing-function);
  border-radius: 10px;
  margin-top: 20px;
  margin-left: auto;
  margin-right: auto;
  padding: 15px 30px;
  font-size: 20px;
  font-weight: 700;
  display: flex;
  position: relative;
  top: 0;
}

.btn:hover {
  background-color: var(--form-sumbit-hover);
}

.btn:active {
  box-shadow: 1px 2px 0 var(--form-sumbit-shadow);
  top: 3px;
}

::-webkit-scrollbar {
  width: 1vw;
}

::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px var(--scroll-track-shadow);
  border-radius: 10px 0 0 10px;
}

::-webkit-scrollbar-thumb {
  -webkit-box-shadow: inset 0 0 6px var(--scroll-thumb-shadow);
  border-radius: 10px 0 0 10px;
}

.color-theme {
  width: 46px;
  height: 46px;
  background-color: var(--bgc-main);
  border: none;
  justify-content: center;
  align-items: center;
  padding: 12px;
  display: flex;
  position: relative;
}

@media screen and (max-width: 1139px) {
  .color-theme {
    margin-top: 20px;
  }
}

@media screen and (min-width: 768px) and (max-width: 1139px) {
  .color-theme {
    margin-left: auto;
    margin-right: auto;
  }
}

@media screen and (min-width: 1140px) {
  .color-theme {
    cursor: pointer;
    margin-left: 10px;
  }
}

.sun {
  width: 40%;
  height: 40%;
  pointer-events: none;
  opacity: 0;
  background: radial-gradient(circle, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0) 50%, #fff 50%);
  border-radius: 2px;
  transition: transform .3s ease-in, opacity .2s ease-in .1s;
  position: absolute;
  transform: scale(.6)rotate(0deg);
}

.sun:before {
  content: "";
  width: 100%;
  height: 100%;
  background: radial-gradient(circle, #fff 30%, rgba(0, 0, 0, 0) 31%, rgba(0, 0, 0, 0) 50%, #fff 50%);
  border-radius: 2px;
  display: block;
  position: absolute;
  transform: rotate(45deg);
}

.sun.visible {
  pointer-events: auto;
  opacity: 1;
  transition: transform .3s ease-in, opacity .2s ease-in .1s;
  transform: scale(1)rotate(180deg);
}

.moon {
  width: 50%;
  height: 50%;
  pointer-events: none;
  opacity: 0;
  background-color: rgba(0, 0, 0, 0);
  border-radius: 50%;
  transition: transform .3s ease-in, opacity .2s ease-in .1s;
  position: absolute;
  top: 18.75%;
  left: 12.5%;
  transform: scale(.3)rotate(65deg);
  box-shadow: 9px 3px #f0f0f0;
}

.moon.visible {
  pointer-events: auto;
  opacity: 1;
  transition: transform .3s ease-in, opacity .2s ease-in .1s;
  transform: scale(1)rotate(0deg);
}

.star {
  width: 0;
  height: 0;
  opacity: 0;
  border-bottom: 5px solid #f0f0f0;
  border-left: 7px solid rgba(0, 0, 0, 0);
  border-right: 7px solid rgba(0, 0, 0, 0);
  transition: all .2s ease-in .4s;
  display: block;
  position: absolute;
  top: 25%;
  left: 5%;
  transform: scale(.55)rotate(35deg);
}

.star:before {
  content: "";
  height: 0;
  width: 0;
  border-bottom: 5px solid #f0f0f0;
  border-left: 3px solid rgba(0, 0, 0, 0);
  border-right: 3px solid rgba(0, 0, 0, 0);
  display: block;
  position: absolute;
  top: -3px;
  left: -5px;
  transform: rotate(-35deg);
}

.star:after {
  content: "";
  width: 0;
  height: 0;
  color: red;
  border-bottom: 5px solid #f0f0f0;
  border-left: 7px solid rgba(0, 0, 0, 0);
  border-right: 7px solid rgba(0, 0, 0, 0);
  display: block;
  position: absolute;
  top: 0;
  left: -7px;
  transform: rotate(-70deg);
}

.moon.visible .star {
  opacity: .8;
}

.star.small {
  opacity: 0;
  transition: all .2s ease-in .45s;
  position: relative;
  top: 50%;
  left: 37.5%;
  transform: scale(.35)rotate(35deg);
}

.moon.visible .star.small {
  opacity: .7;
  transform: scale(.45)rotate(35deg);
}

.form {
  max-width: 440px;
  color: var(--form-text-color-main);
  background-color: var(--form-bgc-color-main);
  transition: transform var(--animation-duration) var(--animation-timing-function);
  border-radius: 30px;
  margin-left: auto;
  margin-right: auto;
  padding: 40px;
  position: relative;
  transform: scale(1);
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

@media screen and (min-width: 768px) {
  .form {
    max-width: 550px;
  }
}

.backdrop--hidden .form {
  transform: scale(1.1);
}

@media screen and (min-width: 1140px) {
  .form {
    width: 100%;
    max-width: 760px;
    padding-top: 20px;
    padding-bottom: 20px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%)scale(1);
  }

  .backdrop--hidden .form {
    transform: translate(-50%, -50%)scale(1.1);
  }
}

@media screen and (min-width: 1100px) and (max-height: 700px) and (orientation: landscape) {
  .form {
    max-width: 780px;
    margin-left: auto;
    margin-right: auto;
    position: static;
    transform: scale(1);
  }

  .backdrop--hidden .form {
    transform: scale(1.1);
  }
}

.form__btn-close {
  background-color: rgba(0, 0, 0, 0);
  position: fixed;
  top: 10px;
  right: 10px;
}

.form__btn-close-icon {
  fill: var(--form-accent-main);
  transition: fill var(--animation-duration) var(--animation-timing-function);
}

.form__btn-close-icon:hover, .form__btn-close-icon:focus {
  fill: var(--form-sumbit-hover);
}

@media screen and (min-width: 1140px) {
  .form__body {
    grid-template: "title title title"
                   "fname none lname"
                   "email none phone"
                   "select none file"
                   "message message message"
                   "agreement agreement agreement"
                   "button button button"
                   / 330px 20px 330px;
    display: grid;
  }

  .form__body .item-a {
    grid-area: title;
  }

  .form__body .item-b {
    grid-area: select;
  }

  .form__body .item-c {
    grid-area: fname;
  }

  .form__body .item-d {
    grid-area: lname;
  }

  .form__body .item-e {
    grid-area: email;
  }

  .form__body .item-f {
    grid-area: phone;
  }

  .form__body .item-g {
    grid-area: message;
  }

  .form__body .item-h {
    grid-area: file;
    justify-self: center;
  }

  .form__body .item-i {
    grid-area: agreement;
  }

  .form__body .item-j {
    grid-area: button;
  }
}

.form__title {
  text-align: center;
  margin-bottom: 30px;
  font-size: 30px;
